import React, { useEffect, useState } from 'react';
import { Button, Col, Row } from 'antd';
import {
  checkToolLogin,
  toolLogin,
  toolLogout,
} from '../../../service/integration-Apis';
import ATable from '../../../components/Core/Table/TableFilter';

let initialState = [
  {
    id: 1,
    img: 'data:image/png;base64,',
    title: 'Accounting Software  Do Beautiful Business',
    tool: 'xero',
  },
];

function Integration() {
  const [loading, setLoading] = useState(true);
  const [data, setData] = useState(initialState);
  const columns = [
    {
      title: 'Tool',
      dataIndex: 'title',
      key: 'title',
      render: (text, record) => (
        <a src="https://identity.xero.com" target="_blank">
          <img
            // id="wp_thbn_37"
            src={record.img}
            height="40"
            width="40"
            alt=""
            data-atf="1"
            data-frt="0"
          />{' '}
          {text}
        </a>
      ),
    },
    {
      title: 'Login Email',
      dataIndex: 'loginEmail',
      key: 'loginEmail',
      align: 'center',
      render: (text) => <a> {text} </a>,
    },
    {
      title: '',
      dataIndex: 'tool',
      key: 'tool',
      align: 'right',
      //   width:'1%',
      render: (text, record) =>
        record.loginEmail ? (
          <Button type="primary" danger onClick={() => logout(text)}>
            Disconnect {text}
          </Button>
        ) : (
          <Button
            type="primary"
            onClick={() => {
              setLoading(true);
              toolLogin(text);
            }}
            loading={loading}
          >
            Connect {text}
          </Button>
        ),
    },
  ];

  useEffect(() => {
    checkLogin();
  }, []);

  const checkLogin = () => {
    checkToolLogin('xero').then((res) => {
      if (res.success) {
        setData([{ ...data[0], ...res.data }]);
      }
      setLoading(false);
    });
  };

  // window.addEventListener('message', event => {
  //     // Only accept messages from http://example.com.
  //     if (event.data === 'close') {
  //         checkLogin()
  //     }
  // })

  const logout = (text) => {
    toolLogout(text).then((res) => {
      if (res.success) {
        setData(initialState);
      }
    });
  };

  return (
    <>
      <Row justify="space-between" align="middle">
        <Col span={24}>
          <ATable
            rowKey="id"
            sticky={true}
            bordered={false}
            columns={columns}
            dataSource={data}
            pagination={false}
          />
        </Col>
      </Row>
    </>
  );
}

export default Integration;
